/**
 * 
 * 
 * @module components/file-viewer/renderers/markdown/utils/path
 */

import type { ResolvedLink, ResolvedImage } from '../markdown.types';

/**
 *  .  ..
 * @param path 
 * @returns 
 */
export function normalizePath(path: string): string {
  const parts = path.split('/');
  const result: string[] = [];

  for (const part of parts) {
    if (part === '..') {
      // 
      result.pop();
    } else if (part !== '.' && part !== '') {
      //  . 
      result.push(part);
    }
  }

  return result.join('/');
}

/**
 * 
 * @param relativePath 
 * @param basePath 
 * @returns 
 */
export function resolveRelativePath(relativePath: string, basePath: string): string {
  // 
  if (relativePath.startsWith('/')) {
    return relativePath.slice(1); //  /
  }

  // 
  const baseDir = basePath.includes('/')
    ? basePath.substring(0, basePath.lastIndexOf('/') + 1)
    : '';

  // 
  const resolvedPath = normalizePath(baseDir + relativePath);
  return resolvedPath;
}

/**
 * 
 * @param href 
 */
export function isExternalLink(href: string): boolean {
  return href.startsWith('http://') ||
    href.startsWith('https://') ||
    href.startsWith('//');
}

/**
 * 
 * @param href 
 */
export function isEmailLink(href: string): boolean {
  return href.startsWith('mailto:');
}

/**
 * 
 * @param href 
 */
export function isAnchorLink(href: string): boolean {
  return href.startsWith('#');
}

/**
 * 
 * @param href  href
 * @param basePath 
 * @returns 
 */
export function resolveLink(href: string, basePath: string): ResolvedLink {
  // 
  if (isAnchorLink(href)) {
    return {
      type: 'anchor',
      path: href,
      originalHref: href
    };
  }

  // 
  if (isExternalLink(href)) {
    return {
      type: 'external',
      path: href,
      originalHref: href
    };
  }

  // 
  if (isEmailLink(href)) {
    return {
      type: 'email',
      path: href,
      originalHref: href
    };
  }

  // 
  const resolvedPath = resolveRelativePath(href, basePath);
  return {
    type: 'internal',
    path: resolvedPath,
    originalHref: href
  };
}

/**
 * 
 * @param src  src
 * @param basePath 
 * @param workspaceId  ID
 * @returns 
 */
export function resolveImage(src: string, basePath: string, workspaceId?: string): ResolvedImage {
  //  data URI
  if (isExternalLink(src) || src.startsWith('data:')) {
    return {
      src,
      originalSrc: src,
      isExternal: true
    };
  }

  //  ID 
  if (!workspaceId) {
    console.warn('[Markdown]  workspaceId:', src);
    return {
      src,
      originalSrc: src,
      isExternal: false
    };
  }

  // 
  if (src.startsWith('/')) {
    return {
      src: `/workspace-files/${workspaceId}${src}`,
      originalSrc: src,
      isExternal: false
    };
  }

  // 
  const resolvedPath = resolveRelativePath(src, basePath);
  return {
    src: `/workspace-files/${workspaceId}/${resolvedPath}`,
    originalSrc: src,
    isExternal: false
  };
}

/**
 *  ID
 *  URL  ID
 * @param text 
 * @param usedIds  ID 
 * @returns  ID
 */
export function generateAnchorId(text: string, usedIds: Set<string> = new Set()): string {
  //  Markdown 
  const plainText = text
    .replace(/\*\*(.*?)\*\*/g, '$1') // 
    .replace(/\*(.*?)\*/g, '$1') // 
    .replace(/`([^`]+)`/g, '$1') // 
    .trim();

  //  URL 
  let id = plainText
    .toLowerCase()
    .replace(/[^\w\s-]/g, '') // 
    .replace(/\s+/g, '-') // 
    .replace(/-+/g, '-') // 
    .replace(/^-|-$/g, ''); // 

  // 
  if (!id) {
    id = 'heading';
  }

  // 
  let uniqueId = id;
  let counter = 1;
  while (usedIds.has(uniqueId)) {
    uniqueId = `${id}-${counter}`;
    counter++;
  }

  usedIds.add(uniqueId);
  return uniqueId;
}
