/**
 * HTML 
 * 
 * @module components/file-viewer/renderers/markdown/utils/sanitizer
 */

import DOMPurify from 'dompurify';

/**
 * 
 */
const ALLOWED_TAGS = [
  // 
  'h1', 'h2', 'h3', 'h4', 'h5', 'h6',
  // 
  'p', 'br', 'hr', 'blockquote', 'div', 'span',
  // 
  'ul', 'ol', 'li',
  // 
  'strong', 'em', 'b', 'i', 'code', 'pre',
  // 
  'a', 'img',
  // 
  'table', 'thead', 'tbody', 'tfoot', 'tr', 'th', 'td',
  // 
  'sup', 'sub', 'del', 'ins', 'mark'
];

/**
 * 
 */
const ALLOWED_ATTR = [
  // 
  'class', 'id',
  // 
  'href', 'title', 'target', 'rel',
  // 
  'src', 'alt', 'width', 'height', 'loading',
  // 
  'data-lang',
  // 
  'data-path', 'data-src'
];



/**
 *  DOMPurify
 */
function createDOMPurify() {
  // 
  if (typeof window !== 'undefined') {
    return DOMPurify(window);
  }
  // SSR 
  return {
    sanitize: (html: string, _options?: any) => html
  };
}

/**
 *  HTML
 * @param html  HTML
 * @returns  HTML
 */
export function sanitizeHtml(html: string): string {
  const purify = createDOMPurify();

  const result = purify.sanitize(html, {
    ALLOWED_TAGS,
    ALLOWED_ATTR,
    ALLOW_DATA_ATTR: false,
    SANITIZE_DOM: true,
    KEEP_CONTENT: true
  });
  return result.toString();
}

/**
 * 
 * @param html HTML 
 * @returns 
 */
export function hasDangerousContent(html: string): boolean {
  const dangerousPatterns = [
    /<script\b/i,
    /<style\b/i,
    /javascript:/i,
    /on\w+\s*=/i, //  onclick= onerror= 
    /<iframe\b/i,
    /<object\b/i,
    /<embed\b/i
  ];

  return dangerousPatterns.some(pattern => pattern.test(html));
}

/**
 *  HTML 
 * @param text 
 * @returns 
 */
export function escapeHtml(text: string): string {
  const div = document.createElement('div');
  div.textContent = text;
  return div.innerHTML;
}
